<script>
export default {
  name: 'Login',
  metaInfo: {
    title: 'Login'
  },
  data () {
    return {
      form: {
        employee_number: '',
        password: ''
      }
    }
  },
  methods: {
    login () {
      this.$store.dispatch('user/loginAction', this.form)
    }
  }
}
</script>

<template>
	<div class="container h-100">
		<div class="d-flex justify-content-center h-100">
			<div class="card mt-5">
        <div 
          v-if="$store.state.user.authenticationError"
          class="alert alert-danger alert-styled-left alert-dismissible"
        >
          <button class="close" data-dismiss="alert"><span>&times;</span></button>
          <span class="font-weight-semibold">Oops! </span> Your credentials are incorrect.
        </div>
				<div class="card-body justify-content-center">
					<div>
						<div class="input-group mb-3">
							<div class="input-group-append">
								<span class="input-group-text"><i class="fa fa-user"></i></span>
							</div>
							<input type="text" name="" class="form-control input_user" v-model="form.employee_number" placeholder="employee number">
						</div>
						<div class="input-group mb-3">
							<div class="input-group-append">
								<span class="input-group-text"><i class="fa fa-lock"></i></span>
							</div>
							<input type="password" name="" class="form-control input_pass" v-model="form.password" placeholder="password">
						</div>
						<div class="form-group">
							<button type="button" name="button" class="btn login_btn" @click="login">Login</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

